import React, { useState, useEffect } from "react";
import {
  ShopContainer,
  Row,
  Image,
  Title,
  LastColumn,
  Quantify,
  Price,
  RemoveRow,
} from "./shop.styles";
import { Message, Button } from "../../components/components.styles";
import baseUrl from '../../url';

const Shop = (props) => {
  const [error, setError] = useState("");
  const [ carrito, setCarrito ] = useState([]);
  useEffect(() => {
    fetch(`${baseUrl}/carrito/listar`)
      .then((data) => data.json())
      .then((productosList) => {
        if (Array.isArray(productosList)) {
          setCarrito(productosList);
        }
        setError("");
      })
      .catch(() => {
        setError("No fue posible recuperar los datos");
      });
  }, []);

  const handleRemoveItem = (_id) => {
    fetch(`${baseUrl}/carrito/borrar/${_id}`, {
      method: "DELETE",
    })
      .then((data) => data.json())
      .then((info) => {
        console.log(info);
        setError("");
      })
      .catch(() => {
        setError("No fue posible recuperar los datos");
        return;
      });
    setCarrito(carrito.filter((el) => el._id !== _id));
  };

  if (!carrito.length) {
    return <Message>No se encontraron articulos en el carrito...</Message>;
  } else {
    return (
      <ShopContainer>
        {carrito.map((item) => (
          <Row key={item._id}>
            <Image src={item.producto.foto} />
            <Title>{item.producto.nombre}</Title>
            <LastColumn>
              <Quantify>{`stock:${item.producto.stock}`}</Quantify>
              <Price>{`$${item.producto.precio}`}</Price>
              <RemoveRow onClick={() => handleRemoveItem(item._id)}>
                &times;
              </RemoveRow>
            </LastColumn>
          </Row>
        ))}
        <Button>finalizar compra</Button>
      </ShopContainer>
    );
  }
};

export default Shop;
