import React, { Fragment, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { MenuContainer, StyledNavLink, MenuItems, UserActions, UserName } from './menu.styles';
import { Button } from '../components.styles';
import CartIcon from '../carrito-icon';
import AuthContext from '../../context/AuthContext';

const Menu = () => {
	const { currentUser } = useContext(AuthContext);
	const history = useHistory();
	return (
		<MenuContainer>
			<MenuItems>
				<StyledNavLink to={'/'}>home</StyledNavLink>
			</MenuItems>
			<UserActions>
				<CartIcon />
				{currentUser === 'administrador' && (
					<Fragment>
						<StyledNavLink to={'/registro'}>registrar producto</StyledNavLink>
					</Fragment>
				)}
				{!!currentUser && (
					<Fragment>
						<UserName>{currentUser}</UserName>
						<Button onClick={() => history.push('/login')}>cambiar de usuario</Button>
					</Fragment>
				)}
			</UserActions>
		</MenuContainer>
	);
};

export default Menu;
