import React, { useState, useEffect } from 'react';
import ProductoDetalle from '../../components/producto-detalle';
import withLoading from '../../components/with-loading';
import { Message } from '../../components/components.styles';
import { ProductoContainer } from './producto.styles';
import baseUrl from '../../url';

const ProductoDetalleWithLoading = withLoading(ProductoDetalle);

const Producto = (props) => {
    const [producto, setProducto] = useState({});
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');

    useEffect(() => {
        const { id } = props.match.params;
        fetch(`${baseUrl}/productos/listar/?id=${id}`)
        .then(data=> data.json())
        .then(producto => {
            if(!!Object.keys(producto).length){
                setProducto(producto);
                setError('');
                setLoading(false);
            } else {
                setError('No fue posible recuperar los datos');
                setLoading(false);
            }
        })
        .catch(()=> {
            setError('No fue posible recuperar los datos');
            setLoading(false);
        });
    }, [props.match.params.id]);

    if(!!error){
        return (<Message>{error}</Message>);
    } else{
        return (
            <ProductoContainer>
                <ProductoDetalleWithLoading loading={loading} producto={producto} />
            </ProductoContainer>
        );
    }
}

export default Producto;