import React, { useContext, Fragment, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { StyledLink } from '../components.styles';
import {
	PDContainer,
	MainContent,
	Image,
	MainData,
	Title,
	Price,
	CTA,
	SubTitle,
	Description
} from './producto-detalle.styles';
import AuthContext from '../../context/AuthContext';
import { Button } from '../components.styles';
import baseUrl from '../../url';

const ProductoDetalle = ({ producto, detalle = true }) => {
	const { currentUser } = useContext(AuthContext);
	const history = useHistory();
	const { error, setError } = useState('');
	const { _id, nombre, descripcion, foto, precio } = producto;

	const handleAdd = () => {
		fetch(`${baseUrl}/carrito/agregar/${_id}`, {
			method: 'POST'
		})
			.then((data) => {
				console.log(data);
			})
			.catch((err) => {
				setError(err.message);
			});
	};
	const handleDelete = () => {
		fetch(`${baseUrl}/productos/borrar/${_id}`, {
			method: 'DELETE'
		})
			.then((data) => {
				history.push('/');
			})
			.catch((err) => {
				setError(err.message);
			});
	};

	return (
		<PDContainer detalle={detalle}>
			<MainContent>
				<Image src={foto} />
				<MainData>
					<Title>{nombre}</Title>
					<Price>{`Precio $${precio}`}</Price>
					<CTA>
						{detalle ? (
							<Fragment>
								<Button onClick={handleAdd}>Agregar al carrito</Button>
								{currentUser === 'administrador' && (
									<Fragment>
										<Button onClick={() => history.push(`/actualizar/${_id}`)}>actualizar producto</Button>
										<Button onClick={handleDelete}>borrar producto</Button>
									</Fragment>
								)}
							</Fragment>
						) : (
							<StyledLink to={`/producto/${_id}`}>Ver detalle</StyledLink>
						)}
					</CTA>
				</MainData>
			</MainContent>
			{detalle && (
				<Fragment>
					<SubTitle>descripci√≥n</SubTitle>
					<Description>{`${descripcion}`}</Description>
				</Fragment>
			)}
		</PDContainer>
	);
};

export default ProductoDetalle;
