import styled from 'styled-components';
import { Span, Paragraph } from '../components.styles';

let detalle = true;

export const PDContainer = styled.div`
    ${props => detalle = props.detalle}
    display: flex;
    flex-direction: column;
    ${()=> detalle ? 'width: 100%;margin: 2rem 0;' : 'width: 26rem;margin: 5rem;'}
    height: auto;
`;
export const MainContent = styled.div`
    ${()=> detalle ?
        `
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        @media (max-width: 1000px) {
            flex-direction: column;
        }` :
        `
        display: flex;
        flex-direction: column;
    `}
`;
export const Image = styled.img`
    ${()=> detalle ?
        `
            width: 50%;
            height: 50%;
            @media (max-width: 1000px) {
                width: 100%;
                height: 100%;
            }
        ` : 'width: 100%;'
    }
`;
export const MainData = styled.div`
    display: flex;
    flex-direction: column;
`;
export const Title = styled(Span)`
    font-size: 2rem;
    height: 4rem;
    ${()=> detalle ? `
        color: #1a1a1a;
        font-size: 10rem;
        height: 35rem;
        @media (max-width: 1000px) {
            font-size: 4rem;
            height: 4rem;
        }
        @media (max-width: 830px) {
            font-size: 2.5rem;
            height: 2.5rem;
        }
    ` : `
        color: #afafaf;
        font-size: 1.8rem;
        height: 1.8rem;
    `}
`;
export const Price = styled(Paragraph)`
    padding-top: 1rem;
    padding-bottom: 1.4rem;
    align-self: flex-end;
    font-size: 3rem;
    @media (max-width: 1000px) {
        font-size: 1.8rem;
    }
`;
export const CTA = styled.div`
    align-self: flex-end;
    display: flex;
    flex-direction: column;
    >*{
        margin-bottom: 2rem;
    }
`;
export const SubTitle = styled.div`
    font-size: 2rem;
    padding-top: 4rem;
    color: #afafaf;
    text-transform: uppercase;
`;
export const Description = styled(Paragraph)`
    padding-top: 3rem;
    flex-grow: 1;
`;