import React from 'react';
import ProductoDetalle from '../producto-detalle';
import { CatalogoContainer } from './catalogo.styles';

const Catalogo = ({ productos }) => (
	<CatalogoContainer>
		{
		    productos.map(producto => <ProductoDetalle key={producto._id} producto={producto} detalle={false} />)
        }
	</CatalogoContainer>
);

export default Catalogo;