import React, { useState, useContext } from 'react';
import { useHistory } from 'react-router-dom';
import { LoginForm, LoginLink } from './login.styles';
import CustomInput from '../../components/custom-input';
import { Notification } from '../../components/components.styles';
import AuthContext from '../../context/AuthContext';

const Login = () => {
	const { currentUser, changeUser } = useContext(AuthContext);
	const [ form, setForm ] = useState({
		usuario: currentUser
	});
	const [ errorMessage, setErrorMessage ] = useState('');
	const history = useHistory();

	const handleSubmit = async (e) => {
		e.preventDefault();
		try {
			changeUser(form);
			history.push('/');
		} catch (err) {
			setErrorMessage(err);
		}
	};

	const handleChange = (e) => {
		const name = e.target.name;
		const value = e.target.value;
		setForm({
			...form,
			[name]: value
		});
	};

	return (
		<LoginForm onSubmit={handleSubmit}>
			<CustomInput
				name={'usuario'}
				value={form.usuario}
				text={'usuario'}
				type={'text'}
				onChange={handleChange}
				required
			/>
			<CustomInput type={'submit'} value={'cambiar'} />
			{!!errorMessage && <Notification>{errorMessage}</Notification>}
		</LoginForm>
	);
};

export default Login;
