import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import { RegistroForm } from './registro.styles';
import CustomInput from '../../components/custom-input';
import { Notification } from '../../components/components.styles';
import baseUrl from '../../url';

const Registro = () => {
	const [ form, setform ] = useState({
		nombre: '',
		descripcion: '',
		codigo: '',
		foto: '',
		precio: '',
		stock: ''
	});
	const [ message, setMessage ] = useState('');
	const history = useHistory();

	const handleChange = (e) => {
		const name = e.target.name;
		const data = e.target.value;

		setform({
			...form,
			[name]: data
		});
	};

	const handleSubmit = async (e) => {
		e.preventDefault();
		console.log(form);
		fetch(`${baseUrl}/productos/agregar`, {
			method: 'POST',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify(form)
		})
			.then((data) => data.json())
			.then((info) => {
				if (Object.keys(info)[0] === 'error') {
					setMessage(info.error);
				} else {
					setMessage(info.notification);
					setform({
						nombre: '',
						descripcion: '',
						codigo: '',
						foto: '',
						precio: '',
						stock: ''
					});
				}
			})
			.catch((err) => setMessage(err.message));
	};

	return (
		<RegistroForm onSubmit={handleSubmit}>
			<CustomInput name={'nombre'} value={form.nombre} text={'nombre'} type={'text'} onChange={handleChange} required />
			<CustomInput name={'descripcion'} value={form.descripcion} text={'descripcion'} type={'text'} onChange={handleChange} required />
			<CustomInput name={'codigo'} value={form.codigo} text={'codigo'} type={'text'} onChange={handleChange} required />
			<CustomInput name={'foto'} value={form.foto} text={'foto'} type={'text'} onChange={handleChange} required />
			<CustomInput name={'precio'} value={form.precio} text={'precio'} type={'number'} onChange={handleChange} required />
			<CustomInput name={'stock'} value={form.stock} text={'stock'} type={'number'} onChange={handleChange} required />
			<CustomInput type={'submit'} value={'registrar'} />
			{!!message && <Notification>{message}</Notification>}
		</RegistroForm>
	);
};

export default Registro;
