import styled from 'styled-components';
import { Link } from 'react-router-dom';

//WHITE #ffffff
//GREEN #cce803
//LIGHT-GREEN #e0ff00
//GREY #afafaf
//BLACK #1a1a1a

export const Span = styled.span`
    font-size: 3rem;
`;
export const Paragraph = styled.p`
    font-size: 1.8rem;
    padding: .5rem 0rem;
`;
export const Notification = styled.p`
    color: #FFB076;
    font-size: 1.4rem;
`;
export const Button = styled.button`
    background-color: #cce803;
    color: #1a1a1a;
    font-size: 1.4rem;
    font-weight: 700;
    text-transform: uppercase;
    outline: none;
    border: none;
    padding: .8rem 5rem;
    cursor: pointer;
    &:hover {
        background-color: #e0ff00;
    }
    &:active {
        background-color: #cce803;
    }
`;
export const StyledLink = styled(Link)`
    text-decoration: none;
    font-size: 1.4rem;
    font-weight: 700;
    color: #1a1a1a;
    &:hover {
        color: #cce803;
    }
    &:active {
        color: #1a1a1a;
    }
`;
export const Message = styled(Paragraph)`
    text-align: center;
    width: 100vw;
    line-height: 100vh;
`;
export const StyledForm = styled.form`
    &>*:not(:last-child) {
        margin-bottom: 2rem;
    }
`;
