import React, { useState, useEffect } from "react";
import Catalogo from "../components/catalogo";
import withLoading from "../components/with-loading";
import { Message } from "../components/components.styles";
import baseUrl from '../url';

const CatalogoWithLoading = withLoading(Catalogo);

const Home = () => {
  const [productos, setProductos] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState("");
  useEffect(() => {
    fetch(`${baseUrl}/productos/listar`)
      .then((data) => data.json())
      .then((productosList) => {
        if (Array.isArray(productosList)) {
          setProductos(productosList);
          setError("");
          if(!productosList.length) setError("No se encontraron articulos...")
        }
        setLoading(false);
      })
      .catch(() => {
        setError("No fue posible recuperar los datos");
        setLoading(false);
      });
  }, []);

  if (!!error) {
    return <Message>{error}</Message>;
  } else {
    return <CatalogoWithLoading loading={loading} productos={productos} />;
  }
};

export default Home;
