import styled from 'styled-components';
import { ReactComponent as Icon } from '../../assets/cart.svg';

export const Container = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
`;
export const CartIcon = styled(Icon)`
    width: 1.8rem;
    height: 1.8rem;
    fill: #cce803;
    &:hover {
        fill: #e0ff00;
    }
    &:active {
        fill: #cce803;
    }
`;
export const Count = styled.div`
    width: 1.5rem;
    height: 1.5rem;
    border-radius: 50%;
    right: -.1rem;
    bottom: -.2rem;
    background-color: #cce803;
    position: absolute;
    font-size: 1.4rem;
    font-weight: 700;
    text-align: center;
    line-height: 1.4rem;
    color: #1a1a1a;
`;