import styled from 'styled-components';

export const InputContainer = styled.div`
    display: flex;
    flex-direction: column;
    padding: 0 1rem;
    & > *:not(:last-child) {
        margin-bottom: .5rem;
    }
`;
export const Label = styled.label`
    font-size: 1.4rem;
    color: #afafaf;
    &::first-letter {
        text-transform: uppercase;
    }
`;
export const Input = styled.input`
    font-size: 1.8rem;
    &[type=submit] {
        text-transform: capitalize;
        background-color: #cce803;
        color: #1a1a1a;
        font-size: 1.4rem;
        font-weight: 700;
        outline: none;
        border: none;
        padding: .8rem 5rem;
        cursor: pointer;
        &:hover {
            background-color: #e0ff00;
        }
        &:active {
            background-color: #cce803;
        }
    }
`;