import React, { useState, useEffect } from 'react';
import { RegistroForm } from './registro.styles';
import CustomInput from '../../components/custom-input';
import { Notification } from '../../components/components.styles';
import baseUrl from '../../url';

const Registro = (props) => {
	const [ form, setform ] = useState({
		nombre: '',
		descripcion: '',
		codigo: '',
		foto: '',
		precio: '',
		stock: ''
	});
	const [ message, setMessage ] = useState('');

	useEffect(() => {
		fetch(`${baseUrl}/productos/listar/?id=${props.match.params.id}`)
			.then((data) => data.json())
			.then((userInfo) => setform({
				nombre: userInfo.nombre,
				descripcion: userInfo.descripcion,
				codigo: userInfo.codigo,
				foto: userInfo.foto,
				precio: userInfo.precio,
				stock: userInfo.stock
			}));
	}, []);

	const handleChange = (e) => {
		const name = e.target.name;
		const data = e.target.value;

		setform({
			...form,
			[name]: data
		});
	};

	const handleSubmit = async (e) => {
		e.preventDefault();
		fetch(`${baseUrl}/productos/actualizar/${props.match.params.id}`, {
			method: 'PUT',
			headers: {
				'Content-Type': 'application/json'
			},
			body: JSON.stringify(form)
		})
			.then((data) => data.json())
			.then((info) => {
				if (Object.keys(info)[0] === 'error') {
					setMessage(info.error);
				} else {
					props.history.push('/');
				}
			})
			.catch((err) => setMessage(err.message));
	};

	return (
		<RegistroForm onSubmit={handleSubmit}>
			<CustomInput
				name={'nombre'}
				value={form.nombre}
				text={'nombre'}
				type={'text'}
				onChange={handleChange}
				required
			/>
			<CustomInput
				name={'descripcion'}
				value={form.descripcion}
				text={'descripcion'}
				type={'text'}
				onChange={handleChange}
				required
			/>
			<CustomInput
				name={'codigo'}
				value={form.codigo}
				text={'codigo'}
				type={'text'}
				onChange={handleChange}
				required
			/>
			<CustomInput name={'foto'} value={form.foto} text={'foto'} type={'text'} onChange={handleChange} required />
			<CustomInput
				name={'precio'}
				value={form.precio}
				text={'precio'}
				type={'number'}
				onChange={handleChange}
				required
			/>
			<CustomInput
				name={'stock'}
				value={form.stock}
				text={'stock'}
				type={'number'}
				onChange={handleChange}
				required
			/>
			<CustomInput type={'submit'} value={'actualizar'} />
			{!!message && <Notification>{message}</Notification>}
		</RegistroForm>
	);
};

export default Registro;
