import React from 'react';
import { useHistory } from 'react-router-dom';
import { Container, CartIcon } from './carrito-icon.styles';

const CarritoIcon = () => {
    const history = useHistory();

    return(
        <Container onClick={()=> history.push('/shop')}>
            <CartIcon />
        </Container>
    );
}

export default CarritoIcon;