import styled from 'styled-components';
import { Span, Paragraph, Button} from '../../components/components.styles';

export const ShopContainer = styled.div`
    display: flex;
    flex-direction: column;
    margin: 5rem auto 0 auto;
    width: 80%;
    & > * {
        margin-bottom: 2rem;
    }
`;
export const Row = styled.div`
    display: inline-flex;
    justify-content: center;
    align-items: center;
    justify-content: space-between;
`;
export const Image = styled.img`
    display: block;
    width: 5%;
    height: 5%;
    @media (max-width: 830px) {
        display: none;
    }
`;
export const Title = styled(Span)`
    font-size: 3rem;
    @media (max-width: 830px) {
        font-size: 1.8rem;
    }
`;
export const LastColumn = styled.div`
    display: flex;
    align-items: center;
`;
export const MoreLesss = styled(Button)`
    color: #cce803;
    float: right;
    font-size: 4rem;
    font-weight: bold;
    cursor: pointer;
    margin: 0 1rem;
    padding: 0;
    background-color: transparent;
    &:hover {
        color: #e0ff00;
        background-color: transparent;
    }
    &:active {
        color: #afafaf;
        background-color: transparent;
    }
    @media (max-width: 830px) {
        font-size: 2rem;
    }  
`;
export const Quantify = styled(Span)`
font-size: 3rem;
@media (max-width: 830px) {
    font-size: 1.8rem;
}
`;
export const Price = styled(Paragraph)`
    font-size: 3rem;
    margin-left: 1rem;
    margin-right: .8rem;
    @media (max-width: 830px) {
        font-size: 1.5rem;
    }  
`;
export const RemoveRow = styled(Button)`
    color: #cce803;
    float: right;
    font-size: 4rem;
    font-weight: bold;
    cursor: pointer;
    margin: 0;
    padding: 0;
    background-color: transparent;
    &:hover {
        color: #e0ff00;
        background-color: transparent;
    }
    &:active {
        color: #afafaf;
        background-color: transparent;
    }
    @media (max-width: 830px) {
        font-size: 2rem;
    }  
`;
export const PriceTotal = styled(Paragraph)`
    align-self: flex-end;
    font-size: 3rem;
    @media (max-width: 830px) {
        font-size: 1.8rem;
    }  
`;